@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Open Sans', sans-serif;
  background-color: lightseagreen;
  scroll-behavior: smooth;
  height: auto;
}

.calculator{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    padding: 20px;
    background-color: #f2f2f2;
    letter-spacing: 1px;
}

.calculator_title{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
}

.calculator_balance>h3{
    margin: 0;
    text-transform: uppercase;
    font-weight: bold;
}

.calculator_balance>h1{
    margin: 0;
}

.calculator_total{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    margin: 10px 0px;
    text-transform: uppercase;
    background-color: white;
    box-shadow: 2px 2px 6px 0px grey;
}

.income{
    width: 50%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    height: 70px;
    border-right: 1px solid gray;
}

.income>h3{
    margin: 0;
}

.income>h3>span{
    color: #339933;

}

.expense{
    width: 50%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    height: 70px;
    border-left: 1px solid gray;
}

.expense>h3{
    margin: 0;
}

.expense>h3>span{
    color: #e60000;
}

.calculator_history{
    width: 100%;
}

.history_title{
    padding: 10px 0px;
    border-bottom:1px solid black;
}

.history_box{
    height: 120px;
    overflow: auto;
    padding: 10px;
}

.history_box::-webkit-scrollbar{
    overflow:hidden;
}

.history{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    padding: 3px 5px;
    margin: 10px 0px;
    background-color: white;
    box-shadow: 2px 2px 6px 0px grey;
}

.green{
    border-left: 7px solid #339933;

}

.red{
    border-left: 7px solid #e60000;
}

.history>h4{
    margin: 2px;
}

.calculator_new_transaction{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 100%;
}

.calculator_input{
    margin: 10px 0px;
    width: 100%;
    display: -webkit-flex;
    display: flex;
   -webkit-flex-direction: column;
           flex-direction: column;
}

.calculator_button{
    background-color: white !important;
}

.app{
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    height: 100vh;
}

.left_app{
    width: 30%;
    background-color: white;
    margin: 30px;
    padding: 20px;
    box-sizing: border-box;
    letter-spacing: 1px;
    text-transform: uppercase;
}

.center_app{
    width: 25%;
    margin: 30px;
}

.right_app{
    width: 30%;
    background-color: white;
    margin: 30px;
    padding: 20px;
    box-sizing: border-box;
    letter-spacing: 1px;
    text-transform: uppercase;
}

@media screen and (max-width:640px) and (min-width:360px) {
    .center_app{
        -webkit-order: 1;
                order: 1;
        width: 100%;
    }

    .left_app{
        width: 100%;
        -webkit-order: 2;
                order: 2;
    }

    .right_app{
        width: 100%;
        -webkit-order: 3;
                order: 3;
       
    }
}
